import React, { Component } from "react";
import { Link } from "react-router-dom";

class YR2018 extends Component {
  render() {
    return (
      <div className="blog">
        <div>
          <h3>Blog Posts for 2018</h3>
          <Link className="bloglink" to="/2018/may/index">
            May
          </Link>
          <Link className="bloglink" to="/2018/june/index">
            June
          </Link>
          <Link className="bloglink" to="/2018/july/index">
            July
          </Link>
          <Link className="bloglink" to="/2018/september/index">
            September
          </Link>
          <Link className="bloglink" to="/2018/october/index">
            October
          </Link>
          <Link className="bloglink" to="/2018/november/index">
            November
          </Link>
          <Link className="bloglink" to="/2018/december/index">
            December
          </Link>
        </div>
      </div>
    );
  }
}

export default YR2018;



