import React, { Component } from "react";
import "./App.css";
import Header from "./components/Header";
import Navigation from "./components/Navigation";
import Footer from "./components/Footer";
import { Route, Redirect, Switch, withRouter } from "react-router-dom";

// NAVIGATION
import Welcome from "./pages/Welcome";
import About from "./pages/About";
import BlogPosts from "./pages/BlogPosts";
import Projects from "./pages/Projects";
import Post from "./components/Post";

// YEAR
import YR2018 from "./pages/years/2018/index2018";
import YR2019 from "./pages/years/2019/index2019";

// *** POSTS FOR 2018 ***
import May from "./pages/years/2018/may";
import June from "./pages/years/2018/june";
import July from "./pages/years/2018/july";
import September from "./pages/years/2018/september";
import October from "./pages/years/2018/october";
import November from "./pages/years/2018/november";
import December from "./pages/years/2018/december";

// *** POSTS FOR 2019 ***
import January from "./pages/years/2019/january";
import Breadcrumbs from "./components/Breadcrumbs";

class App extends Component {
  state = { links: [] };
  componentDidUpdate(previousProps, previousState) {
    console.log(previousProps.location, this.props.location);

    if (previousProps.location.pathname !== this.props.location.pathname) {
      const links = [{ link: this.props.location.pathname, label: "test" }];
      this.setState({
        links
      });
    }
  }

  render() {
    const { links } = this.state;
    return (
      <div className="App" id="backToTop">
        <Header />
        <Navigation />
        {/* <Breadcrumbs links={links} /> */}
        <Route exact path="/" render={() => <Redirect to="/welcome" />} />
        <Switch>
          <Route exact path="/welcome" component={Welcome} />
          <Route exact path="/about" component={About} />
          <Route exact path="/blogPosts" component={BlogPosts} />
          <Route exact path="/projects" component={Projects} />

          {/* 
          2018 ROUTES
          */}
          <Route exact path="/years/2018" component={YR2018} />
          <Route exact path="/2018/may/index" component={May} />
          <Route exact path="/2018/june/index" component={June} />
          <Route exact path="/2018/july/index" component={July} />
          <Route exact path="/2018/september/index" component={September} />
          <Route exact path="/2018/october/index" component={October} />
          <Route exact path="/2018/november/index" component={November} />
          <Route exact path="/2018/december/index" component={December} />

          {/* 
          2019 ROUTES
          */}
          <Route exact path="/years/2019" component={YR2019} />
          <Route exact path="/2019/january/index" component={January} />

          <Route
            exact
            path="/:month"
            render={() => "Nothing to see here. Shoo. Shoo!"}
          />
        </Switch>
        <Route path="/:month/:post" component={Post} />
        <Footer />
      </div>
    );
  }
}

export default withRouter(App);



