import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class DecBlog4 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>Step On the Break</h2>
          <p>Official Post Date: 12/23/18</p>
          <p />
          <h3>Contents</h3>
          <ol>
            <li>
              <a href="week">Week 9: We've Got the Power</a>
            </li>
            <li>
              <a href="#path">Finding the Path: File I/O</a>
            </li>
            <li>
              <a href="#outlet">The Power Cord: Buffers and Writers</a>
            </li>
            <li>
              <a href="#power">POWERRRRRRRRRR!!</a>
            </li>
          </ol>

          <h3 id="week">Week 9: File I/O</h3>

          <p>
            Week 9 and the grind continues. Well, kinda. With Christmas next
            Tuesday, there was no class this Thursday (12/20). That break
            continues: there is no class next Tuesday (Christmas Day), Thursday
            (12/27) or Saturday (12/29), and then we are off on New Years Day.
            Our next official class is not until January 3rd. Because we have
            such a long break, I have created Operation: Step On the Break.
          </p>

          <p>
            Step On the Break is just a way for me to prioritize what projects
            to focus on, when to review Java chapters, how to review SQL
            material, and formally introduce myself to algorithms (YAY!). I have
            broken up all of the tasks into three separate tiers, with tier 1
            being the most important and tier 3 being the least important. Based
            on those tiers, I then scheduled what to work on each day of the
            next two weeks.
          </p>

          <p>
            The post for this week - post number nine (wow) - will focus on
            Chapter 15: How to Work with File I/O. Each entry will discuss a
            different part of an in-class project that demonstrates how it
            works. Entry 1 will be about the process of finding the path to link
            Java and a text file; Entry 2 will be about creating the connection;
            and Entry 3 will round it out and discuss file I/O in action.
          </p>

          <p>Well, let's get started!</p>

          <h3 id="path">Finding the Path: File I/O</h3>

          <p>
            For class on Tuesday, we went over the subject of file inputs and
            file outputs, or <em>file I/O</em>. In the broader scheme of things,
            this is a preview of how an application can both read and write data
            to a database, which is what our capstone project will eventually
            do. For the purposes of this chapter and the in-class demonstration,
            we got to see this in action using a regular text file. Walk with
            me, if you will.
          </p>

          <p>
            Within Java, there are two important packages for reading and
            writing data to a file: java.io and java.nio.file. The java.nio.file
            package contains two classes that will be shown below. The first
            one, the Path class, has methods that allow developers to retrieve
            information about file directories as well as the files themselves.
            The File class, on the other hand, has methods that can determine
            whether or not a file is read-only or writable, create directories
            and also create new files. So, what does this look like?
          </p>

          <CodeExample
            codeString={`
  import java.io.*;
  import java.nio.file.DirectoryStream;
  import java.nio.file.Files;
  import java.nio.file.Path;
  import java.nio.file.Paths;
  import java.util.ArrayList;
  
  public class demoDay22App {
  
    public static void main(String[] args) throws IOException {

      System.out.println("Hello");
      
    // ***USING THE PATHS AND FILES CLASSES FROM JAVA.NIO.FILE PACKAGE***
      
      // create a folder on c:/ if it doesn't exist
      String dirString = "/xxxxxxx/xxxxxxx/documents/bootcamp/files";
      
      // how to use the get() method from the Paths class
      // as well as creating a new directory using the File class
      Path dirPath = Paths.get(dirString);
      if(Files.notExists(dirPath)) {
        Files.createDirectories(dirPath);
      }
      
      // create a file in this new folder
      String fileString = "stuffies.txt";
      Path filePath = Paths.get(dirString, fileString);
      
        // the if statement checks whether or not there is a stuffies.txt file
        // if it does not exist, then the createFile method creates it
        if(Files.notExists(filePath)) {
          Files.createFile(filePath);
        }

          `}
          />

          <p>
            Using the Java API, the Path and File classes are imported to the
            demoDay22App class. The import statements in the example show each
            othe individual classes used: java.nio.file.Files,
            java.nio.file.Path, java.nio.file.DirectoryStream - this is
            acceptable. Another way to import them would be write an import
            statement that imported <em>all</em> of the classes. It would look
            like this: <code>import java.nio.file.*;</code>
          </p>

          <p>
            If the application is going to connect to a source like a text file,
            then there has to be path between the two. In the example above, the
            path created to connect the two starts with the Path object{" "}
            <code>Path dirPath = Paths.get(dirString)</code>. This variable
            stores the directory path as a string, and determines where the app
            will direct its attention.
          </p>

          <p>
            The next step involves using the File class methods to officially
            create a directory. That if statement in the next block says, If{" "}
            <code>dirPath</code> does not exist, then create a new folder in
            this location. So what does it do? It creates a folder exactly where
            you tell it! On the hard drive, it follows the dirPath that we
            specified: starts with the user directory, then goes to the
            Documents folder, lands on the bootcamp folder - checks to see if
            there is a directory (which there was not) - and then creates a
            brand new folder (files).
          </p>

          <p>
            Now that a new directory has been created, the next block creates a
            file. Before creating the file, the file name is stored in a String
            variable called fileString. It doesn't have to be named fileString,
            but that's what we used in class. Next, we need a way to store the
            fileString variable and directory path to tell Java where to look.
            The <code>Path filePath = Paths.get(dirString, fileString)</code>{" "}
            does just that. The filePath variable represents the path to the
            file on the hard drive.
          </p>

          <p>
            Just like you saw a moment ago, an if statement is used to check
            whether or not there is a file in our desired location. If filePath
            does not exist, then create a new file at that location called
            "stuffies.txt" (don't ask why it's called stuffies).
          </p>

          <p>
            By importing the java.nio.file classes, creating a path to our
            desired destination and a brand new file, the application can now
            perform even more tasks. In the next entry, I'll try to explain how
            streams connect Java and the file system, serving as the cord to
            link the two together. And then POWERRRRRRRRRR.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="outlet">The Power Cord: Buffers and Writers</h3>

          <p>
            So the path has been established from the Java application to the
            stuffies.txt file (uggggh). If the application is the appliance,
            then we'll say the file is the outlet, and we need a cord that links
            the two together so the application can work. To create that cord,
            the File class from the java.io package needs to be invoked.
          </p>

          <p>
            The first code block written in the example below shows how the
            application creates a <em>stream</em> for writing data to the
            stuffies file. The first statement uses the toFile method from the
            File class to make the filePath a File object. And then the files
            within the files are filed away to one file which the other files
            can then file files.
          </p>

          <p>Ignore that last sentence.</p>

          <CodeExample
            codeString={`
    // how to write data TO the stuffies.txt file
    File stuffiesFile = filePath.toFile();
    PrintWriter out = new PrintWriter( 
              new BufferedWriter(  						  
              new FileWriter(stuffiesFile)));
    out.println("1\tBulbasaur\tSmall\tGreen");
    out.println("2\tCharmander\tMedium\tRed");
    out.println("3\tPikachu\tLarge\tYellow");
    
    out.close();

          `}
          />

          <p>
            The important part is that the stuffiesFile object can be passed
            through the the PrintWriter object to officially create the{" "}
            <em>output stream</em>. Have you ever seen one of those diagrams
            that shows a cross-section of something sliced open so you can see
            the layers inside of it? The PrintWriter statement is just that: it
            is a series of layers that create the cord which can pass data from
            the application to the stuffies.txt file.
          </p>

          <p>
            For the output itself, the <code>out</code> object with the println
            methods write data to the stuffies file. Because I don't know what a
            stuffy is, I decided to use Pokemon instead. Below, you'll see
            Bulbasaur, Charmander, and Pikachu are now in our Pokedex.
          </p>

          <p>NOTE: THIS. IS. AWESOME.</p>

          <p>
            If you take a look at the actual stuffies file (UGGGGGGH), then you
            will see the three Pokemon entered into the Pokedex.
          </p>

          <p>Do you know what this means? What the implications are? </p>

          <p>
            If we wanted, we could catch all of the Pokemon and add them to the
            text file. Like a real Pokedex.
          </p>

          <p>
            But what if we wanted the app to read input and print it to the
            console? How would that work?
          </p>

          <CodeExample
            codeString={`
  // how to read FROM the stuffies.txt file
  BufferedReader in = new BufferedReader(
            new FileReader(stuffiesFile));
  
  String line = in.readLine();
  
  // add while statement to print all lines from stuffiesFile.txt
  ArrayList<Stuffy> stuffies = new ArrayList<>();
  while(line != null) {
    
    // split the data into an area
    String[] fields = line.split("\t");
    
    // convert data from file
    int id = Integer.parseInt(fields[0]);
    String type = fields[1];
    String size = fields[2];
    String color = fields[3];
    
    // create new object with converted types
    Stuffy s = new Stuffy(id, type, size, color);
    stuffies.add(s);
    line = in.readLine();
  }	
  in.close();     

          `}
          />

          <p>
            Well, we have to create another cord to receive input from the text
            file. Similar to the output stream, we need to create an{" "}
            <em>input stream</em> so the application can read the data from the
            text file. In order to read the data, there are three steps to make
            the input cord.
          </p>

          <p>
            The first step is to create the BufferedReader object so data can be
            passed in to the application. The BufferedReader object called{" "}
            <strong>in </strong>
            will allow the app to read lines of data from the text file and
            print it to the console. But before that can happen, the data coming
            in needs to be received, orgnized and distributed so Java can use
            it.
          </p>

          <p>
            In the example above, the next step is to create the warehouse that
            will hold the data from the text file. That warehouse is the
            stuffies ArrayList object. Next, we need a conveyer belt to loop
            through all of the items being received from the text file. For our
            list of Pokemon, the while loop will pull in each of the Pokemon for
            processing. The <strong>fields </strong> array will split the pieces
            of data for each Pokemon and place it into an array. Once the while
            loop processes each one and reaches null, the type, size and color
            can now be used in Java.
          </p>

          <p>
            With the data broken up into readable pieces for Java, it can now be
            reconstructed. The next block packages the data and prints it to the
            console.
          </p>

          <p>Whew.</p>

          <p>
            Now that there is a cord to write to the file as well as to read
            from the file, we now have...
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="power">POWERRRRRRRRRR</h3>

          <p>
            This is AWESOME. Instead of the application only temporarily storing
            the data entered, the text file serves as a kind of database to
            store it over time. Small step, I know, but it is a pretty cool step
            to take!
          </p>

          <p>
            The ability to enter data, <em>save it</em>, and then read it in the
            console application is fantastic. This opens up all kinds of
            opportunities when creating applications in the future.
          </p>

          <p>I can't wait for what's next!!!</p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
        </div>
      </div>
    );
  }
}

export default DecBlog4;



