import React, { Component } from "react";
import { Link } from "react-router-dom";

class YR2019 extends Component {
  render() {
    return (
      <div className="blog">
        <div>
          <h3>Blog Posts for 2019</h3>
          <Link className="bloglink" to="/2019/january/index">
            January
          </Link>
          <Link className="bloglink" to="/february">
            February
          </Link>
          <Link className="bloglink" to="/march">
            March
          </Link>
          <Link className="bloglink" to="/april">
            April
          </Link>
          <Link className="bloglink" to="/may">
            May
          </Link>
        </div>
      </div>
    );
  }
}

export default YR2019;



