import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class DecBlog5 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>Update 2.0.0: Growing Pains</h2>
          <h3>Contents</h3>
          <ol>
            <li>
              <a href="#what">What. Happened.</a>
            </li>
            <li>
              <a href="#bones">Good Bone Structure</a>
            </li>
            <li>
              <a href="#nice">Look Good Feel Good</a>
            </li>
            <li>
              <a href="#reflect">Short Reflection</a>
            </li>
          </ol>

          <h3 id="what">What. Happened.</h3>
          <p>
            Training Ground has now been live for over six months and contains
            over 14 blog posts. I remember when Brandon had asked if I wanted to
            make it a static site through GitHub Pages back in June 2018 and how
            terrifyingly exciting that was. In that time, I transitioned from
            support to account operations; I started coding school back in
            October; and, Training Ground has grown from just two months of blog
            posts to seven.
          </p>

          <p>
            With the new year came new opportunities. As much as I love Training
            Ground and what I've created up to this point, there are a few
            glaring issues with it. One of those problems is scalability. If you
            remember, the welcome page used to list each of the months in the
            Navigation component, which, from a user standpoint, is not
            aesthetically pleasing or intuitive.
          </p>

          <p>
            Another issue with Training Ground was the option of displaying the
            trail of pages the user chooses as they explore the site. There was
            no way to click a link to take the user back to the previous page
            they were at or a way simply go back to the beginning without
            clicking the Home link or clicking the browser back button. Again,
            not the greatest user experience.
          </p>

          <p>
            Both of these issues are valid problems with the site that needed to
            be addressed. As a newbie in the software development space, it has
            taken some time to fully grasp all of the steps needed to solve
            these. In addition to that, school has occupied a lot of my time as
            well and rightfully so. As much as I love working on Training
            Ground, it is still a side project.
          </p>

          <p>
            Today, I want to give an overview of what has changed, why it
            changed, and what will happen with the site moving forward. If I'm
            being completely honest, all of this has been awesome. These kinds
            of issues get me excited because I'm learning and sharpening my
            skills.
          </p>

          <h3 id="bones">Good Bone Structure</h3>

          <p>
            When Brandon and I first set up Training Ground, the directory for
            all of the posts was quick and dirty. There was no thought about how
            the site would scale with the increasing number of blog posts that
            would be written or leaving room for other opportunities to be
            added. We took the happy path and just wanted to get something off
            the ground.
          </p>

          <p>
            The structure of the site directory was the root of the problem. In
            order to make the site able to handle additional blog posts in an
            efficient way, the directory and the paths of the components needed
            to be updated.
          </p>

          <p>
            The first step was planning how to reshuffle the directory and the
            paths of the components. This entire process scared the crap out of
            me. I was going to be breaking the bones of the site and crippling
            it whether I liked it or not. The site would not be able to walk for
            a bit in the development environment, so I wanted to make sure I had
            a plan for each of these major changes.
          </p>

          <p>
            To help with the planning, I went into work on a Sunday morning so I
            could use a whiteboard. I drew a high-level view of the site, each
            of the components and their relative paths, and sketched how each of
            them would change. After about an hour and a half or so, I decided
            to put the plan into action.
          </p>

          <p>And guess what? I brokeded everything. All of it.</p>

          <p>
            Right off the bat, my terminal was screaming at me. When I think
            about it now, I laugh to myself at how stressed I was to get it all
            working. I had added three new directories: a years directory, and
            then two subdirectories called 2018 and 2019. The years directory
            serves as a new path that can hold posts in their respective years.
            With the new 2018 and 2019 subdirectories, I had to move allllll of
            the blog posts from the past year to the new 2018 folder and then
            update each of their paths so the site would know where to go to
            retrieve the posts.
          </p>

          <p>
            Testing was my best friend. I made a concerted effort to try{" "}
            <em>everything</em> to get all of this to work. Even with the best
            laid plan, there were other issues popping up that wanted to stop
            the show. Some of them included syntax errors, like forgetting a
            closing tag or misspelling a tag name; others involved making sure
            there was an extra period in the directory path. This whole process
            gave me a glimpse of just how important attention to detail truly
            is.
          </p>

          <p>
            After some time tinkering with each of the components, I{" "}
            <strong>
              <em>finally</em>
            </strong>{" "}
            got it to work. The bones of the site were no longer busted and
            broken. My terminal wasn't yelling at me about some error in the
            app.js or post.js components. All was quiet on the development
            front. With that out of the way, it was time to think about updating
            the contents of the site.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="nice">Look Good Feel Good</h3>

          <p>
            With the site's new structure, the next step was getting a
            (somewhat) new outfit. At the beginning, I mentioned the need for
            the site to be scalable and intuitive for the user. Now that the
            Navigation component doesn't list all of the months, there was room
            to play with a new look.
          </p>

          <p>
            Instead of there being a long list of months, Navigation now has
            four options to choose from: Home, About, Code Posts, and a new
            Projects page. Code Posts will take the user to the years page which
            then shows all of the blog post options available to read. The 2018
            and 2019 buttons will take the user to a page which lists all of the
            months for that year. When the user chooses the month, the list of
            posts for that month will be listed and then they can click one of
            the posts to read. Simple and effective.
          </p>

          <p>
            The new Projects page is really exciting. If I do become a competent
            software developer, the Projects page will be a place where I'll
            provide links and descriptions to the various side projects that I
            work on. I cannot overstate how excited I am about this. I can't
            wait to work on multiple projects and continue learning and
            sharpening my skills.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="reflect">Short Reflection</h3>

          <p>
            I'm going to keep this short and sweet. If you haven't gathered this
            yet, then I'll repeat it again: the level of excitement I have
            moving foward is off the charts. The feeling I get when I code is
            the same feeling I get when I play soccer. I love it.
          </p>

          <p>See you next time!</p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
        </div>
      </div>
    );
  }
}

export default DecBlog5;



