import React, { Component } from "react";
import { Link } from "react-router-dom";

class November extends Component {
  render() {
    return (
      <div className="blog">
        <div>
          <h3>Blog Posts for November</h3>
          <Link className="bloglink" to="/november/blog1">
            The One About Coming Together (11.4.18)
          </Link>
          <Link className="bloglink" to="/november/blog2">
            The One About Java - Part 1 (11.11.18)
          </Link>
          <Link className="bloglink" to="/november/blog3">
            The One About Java - Part 2 (11.17.18)
          </Link>
          <Link className="bloglink" to="/november/blog4">
            The One About Java - Part 3 (11.25.18)
          </Link>
        </div>
      </div>
    );
  }
}

export default November;



