import React, { Component } from "react";

class About extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>About</h2>

          <h3>About this app</h3>

          <p>
            There's not much here, as you can tell. Not a lot to view when you
            click on those tabs. I know. It's terrible.
          </p>

          <p>But that means there's room for opportunity.</p>

          <p>
            As time goes on, there will be new experiences to document, new
            programming pieces to explore and share, and new projects to
            showcase. This is a messy exhibit at its core.
          </p>

          <p>
            Coding should be about having fun, making mistakes, and learning
            from those mistakes to make something better. That's the goal I'm
            shooting for - to learn, to have fun, and to fail forward.
          </p>

          <h3>Support Network</h3>

          <p>
            First and foremost, I want to thank the following list of people
            that have helped guide me every step of the way.
          </p>

          <p>That list includes:</p>

          <ul>
            <li>Sir Brandon Clemons</li>
            <li>Rebekah Bastian</li>
            <li>Corina Kolbe</li>
            <li>John Penn</li>
            <li>Mi Madre</li>
          </ul>

          <p>
            Brandon is the man. I could not have made the journey without his
            input, guidance, and patience. The dude is a wizard.
          </p>

          <p>
            As for Rebekah and Corina, I will ALWAYS be in their debt. This
            opportunity would not have been possible without their influence and
            vision.
          </p>

          <p>
            John has been instrumental in making sure I have the necessary tools
            to succeed ever since this began. He has made sure the path has been
            as clear as possible all the way through.
          </p>

          <p>
            My mom needs no introduction. She has been, and always will be,
            supportive and understanding. If I can reach the goal at the end of
            all of this, I know she'll be proud.
          </p>

          <h3>The Up and Up</h3>

          <p>
            With that being said, take a look around and be sure to send any
            feedback. I am more than happy to take constructive feedback to
            better the site. Or if you just wanna say Hi, that works as well!
          </p>

          <p>
            Also, if you wanna check out my{" "}
            <a href="https://github.com/goomba5" target="_blank">
              GitHub account
            </a>
            , you are more than welcome to take in all of the uneventful
            activities going on there.
          </p>

          <p>But most importantly...</p>

          <h2>THANK YOU!</h2>
        </div>
      </div>
    );
  }
}

export default About;



