import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class DecBlog3 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>Plan Of Attack</h2>
          <p>Official Post Date: 12/16/18</p>
          <h3>Contents</h3>
          <ol>
            <li>
              <a href="#parallel">Week 8: Parallels</a>
            </li>
            <li>
              <a href="#passing">Passing Drills</a>
            </li>
            <li>
              <a href="#tech">Technical Challenges</a>
            </li>
            <li>
              <a href="#speed">Speed Tests</a>
            </li>
            <li>
              <a href="#scrim">Scrimmages</a>
            </li>
            <li>
              <a href="#conclusion">What's Next?</a>
            </li>
          </ol>

          <h3 id="parallel">Week 8: Parallels</h3>

          <p>
            If there is one thing that has been tough to grasp since the
            beginning of this year, it has to be the anxiety that comes with
            exploring all of the topics that come with coding. Yes, you read
            that correctly: the hardest part has been figuring out the best
            possible process to learn how to code. Before you make any
            judgements, please hear me out.
          </p>

          <p>
            Learning to code has been one of the best experiences in my life.
            Coding involves learning the tools of the trade, mastering those
            tools, and using them to solve problems creatively. That first part
            - <em>learning</em> the tools - is not so bad. Before bootcamp even
            began, Brandon was (and still is) the best source for mapping out
            where to go, what to look for and how to approach it. But what about{" "}
            <em>how</em> to study? That is something I've had to solve on my
            own.
          </p>

          <p>
            So what did I come up with? Well, if you know me, then you know that
            soccer serves as the template of my life. I love coding, but how
            could I channel that energy and make it useful? I had to find some
            parallels.
          </p>

          <h3 id="passing">Passing Drills</h3>

          <p>
            When I think about soccer, I think about the basics. At its very
            core, you have to have a ball to use - that's obvious. Code, in this
            case, serves as our round ball to use. But what else is there? What
            other aspects of soccer can be applied to coding? The first answer I
            landed on was passing.
          </p>

          <p>
            Soccer, much like coding, is performed within a team setting. And
            when you are a part of team playing a sport that requires moving the
            ball from person to person, you have to know how to pass it. This
            idea led me to create a regiment that I call passing drills.
          </p>

          <p>
            Passing drills is nothing more than getting some time to review the
            topics in each chapter and write simple statements. There is nothing
            strenuous about it. It is used to build familiarity with the coding
            topics, play around with different ideas and test any lingering
            assumptions (and watch the IDE kick it back in my face).
          </p>

          <p>
            This exercise has been great to do at the start of the day as well
            as when I get home from work. It's a good way to ease my way into
            doing a larger project that is either our homework assignment or a
            personal project that I use to practice. Either way, it's an easy
            way to build simple skills.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="tech">Technical Challenges</h3>

          <p>More sports analogies incoming. I'm really sorry.</p>

          <p>
            An important factor in any sport, whether it's soccer, basketball,
            hockey - curling (okay, maybe not curling) - is agility. How quickly
            can a player manuever in tight spaces or accelerate from a standing
            position to a full speed sprint? Understanding how to set your feet
            and leverage your body is important when it comes to determing your
            direction. In soccer especially, agility exercises are crucial. With
            coding, the ability of a developer to understand a problem or task
            and plan for what steps to take - just like a player determing which
            direction to go - is equally as important.
          </p>

          <p>
            The idea behind technical challenges is to zero in on specific steps
            in a project that may have caused some trouble. The ultimate goal is
            learn what steps could have been taken to improve the code. An
            example of this could include surveying whether or not another class
            could be created so the application is more modular. In another
            example, maybe a for loop or a method could be more optimized than
            it was originally written. These seemingly small steps can make a
            lot of difference in determing an application's direction when it's
            being built.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="speed">Speed Tests</h3>
          <p>
            As an athlete, who doesn't like to race? To test how fast you are
            against other opponents? Speed tests are straightforward: once every
            morning and evening, I give myself thirty minutes (hoping to cut to
            fifteen) to complete a project as fast as possible. To track
            progress, there is an Excel sheet that records the build time (how
            quickly the application is built with valid data), and then there is
            the debug time.
          </p>

          <p>
            This activity was the first one that I came up with when trying to
            figure out how to study. When I was younger, I loved racing my
            friends and getting those moments to showcase how fast I was
            compared to players on opposing teams. This activity taps into that
            experience.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="scrim">Scrimmages</h3>

          <p>
            Last but not least, you have to be able to take everything you've
            learned and perform. Because the goal is to build the necessary
            skills for the real game, scrimmages are vital to the process. There
            is no better place to take everything you've learned to test it,{" "}
            <em>fail</em> and then <em>learn</em> from those mistakes in real
            time. Scrimmages are always fun because they are an activity that
            can hint towards some of the patterns that will be present during a
            real game.
          </p>

          <p>
            The purpose of a scrimmage is simple: write the documentation for a
            simple application using the topics covered in class, devide the
            work into modules, and then complete each of the modules for the
            project by the end of the week. These projects will require
            thoughtful planning and execution.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="conclusion">What's Next?</h3>
          <p>Time to practice! See you next time!</p>
        </div>
      </div>
    );
  }
}

export default DecBlog3;



