import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class NovBlog1 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Coming Together</h2>
          <p>Official Post Date: 11/3/18 (Early post!)</p>
          <p>Time: 8:48am</p>
          <h3>Join the Party</h3>

          <p>Entry Creation Date: 11/1/18</p>
          <p>
            Here we are: day one, week two of the coding bootcamp. Work was fun,
            as always. Lots of calls and things like that.
          </p>

          <p>
            Today's class continued to focus on SQL and some of the basics like
            creating a schema and writing <code>Select</code> statements, but we
            also learned a little more about the <code>JOIN</code> clause
            command thing.
          </p>

          <p>
            Much like I had discussed in{" "}
            <a href="https://goomba5.github.io/october/blog2" target="_blank">
              last week's blog post
            </a>
            , SQL allows developers to create databases which house tables of
            data that can be used to read information, update it, as well as
            delete it (CRUD). Tonight, we reviewed the data definition side of
            things, but we also touched on an aspect of data manipulation known
            as Joins.
          </p>

          <CodeExample
            codeString={`
              Select orders.customerID, customer.name, orders.date, orders.total
              From	Orders
              Join	customer 
              On	orders.customerID = customer.id;
          `}
          />

          <p>
            To set up a join, we first want to select the columns that we want
            to see. In the example, we settled on selecting the customerID, date
            and total from the Orders table, as well as the name from the
            customer table. On the next line we write <code>join customer</code>
            . This line says to SQL, I want to combine columns from two (or
            more) tables and present that information in the result grid. The
            type of join here is an <em>inner join</em>, so the next line allows
            us to join the tables together <code>ON</code> the orders foreign
            key (customerID) and the customer primary key (ID) because they both
            refer to each other.
          </p>

          <p>
            To take it a step further, our instructor introduced what are known
            as aliases. As you could see in the example, the first part of our
            SQL script refers to each of the table names when referencing the
            customerID, name, date and total by putting the table name in front
            of each attribute. What if we are dealing with more than two tables?
            Do we have to type <em>every</em> name each time or can we shorten
            it somehow? Enter aliases. An alias in SQL allows us to name the
            tables (and columns!) what we want without sacrificing the
            reference. We can even use an alias for the columns in the{" "}
            <code>select</code> statement to present the information in a more
            formal sense rather than seeing customerID.
          </p>

          <CodeExample
            codeString={`
              Select o.customerID 'Customer ID', c.name AS 'Customer Name', 
              o.date 'Date', 
              o.total 'Total'
              From	Orders o
              Join	customer c
              On	o.customerID = c.id;
          `}
          />

          <p>
            For the tables, Orders can be referred to as 'o' and Customer can be
            referred to as 'c'. In addition, the o.customerID is now Customer ID
            - looks WAY better - and c.name is Customer Name (hallelujah!) With
            aliases, SQL scripts and the result grid become that much more
            readable. Hooray reaadability!
          </p>

          <h3>More SQL, but with Movies</h3>
          <p>Entry Creation Date: 11/3/18</p>

          <p>
            Over the last few class sessions, our instructor has mentioned a
            number of companies that utilize databases regularly. From Amazon to
            Netflix, databases are being used to store all kinds information
            that can be stored and manipulated on a daily basis. In an effort to
            mirror IMDb, we had to create our own schema that contained tables
            of information for our favorite movies.
          </p>

          <CodeExample
            codeString={`
            -- create bmdb database
            drop database if exists bmdb;
            create database bmdb;
            
            use bmdb;
        
            -- create movie table
            CREATE TABLE movie (
              ID        int   primary key auto_increment,
              title     varchar(100) not null,
              rating    varchar(5)   not null,
              year      int          not null,
              director  varchar(50)  not null
            );
            
            -- create actor table
            CREATE TABLE actor (
              ID        int   primary key auto_increment,
              firstName varchar(25)  not null,
              lastName  varchar(25)  not null,
              gender    varchar(6)   not null,
              birthDate date         not null
            );
          
          `}
          />

          <p>
            With the tables created, we could then insert data into them. These
            tables will store the titles for all of the movies, the actors'
            first and last names (and birth dates, which is creepy), and the
            genre
          </p>

          <CodeExample
            codeString={`
            -- insert values for movie table
            INSERT INTO movie
              (title, rating, year, director)
            VALUES
              ('Avengers: Infinity War', 'PG', 2018, 'Joe Russo'),
              ('John Wick', 'R', 2014, 'Chad Stahelski'),
              ('Star Wars: The Last Jedi', 'PG-13', 2017, 'Rian Johnson'),
              ('Spirited Away', 'PG', 2001, 'Hayao Miyazaki');
              
            -- insert values for the actor table
            INSERT INTO actor
              (firstName, lastName, gender, birthDate)
            VALUES
              ('Daisy', 'Ridley', 'Female', '1992-04-10'),
              ('Keanu', 'Reeves', 'Male', '1964-09-2'),
              ('Chadwick', 'Boseman', 'Male', '1977-11-29'),
              ('Daveigh', 'Chase', 'Female', '1990-07-24');
          `}
          />

          <p>
            And that was the gist of it! All of the CRUD, creating and
            manipulating the data in the tables, has been covered.
          </p>

          <p>
            I really really wish I had learned this stuff sooner. This is so
            much fun.
          </p>
        </div>
      </div>
    );
  }
}

export default NovBlog1;



