import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class SeptBlog1 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Anxiety</h2>

          <h3>Going the Distance</h3>
          <p>
            This is the first blog post of September, and, let me tell ya...
          </p>

          <p>
            I am <em>extremely</em> anxious, and it is crippling me. It has been
            hard to keep my studies in perspective and remind myself to relax.
          </p>

          <p />
        </div>
      </div>
    );
  }
}

export default SeptBlog1;



