import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class NovBlog3 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Java - Part 2</h2>
          <p>Official Post Date: 11/17/18 (Early post!)</p>
          <p>Time: 8:18pm</p>
          <h3>Contents</h3>
          <ol>
            <li>Week 4: All the Feels</li>
            <li>
              <a href="#day1">Day 1 Reflection: Weird Sentence, But Ok</a>
            </li>
            <li>
              <a href="#day2">Day 2 Reflection: The Powers That Be</a>
            </li>
            <li>
              <a href="#day3">Day 3 Reflection: Error of Your Ways</a>
            </li>
          </ol>

          <h3>Week 4: All the Feels</h3>

          <p>
            While I was in college, the best course I had ever taken was
            Sociology of the Black Community. It was a three-part course that
            took place over three quarters (this was before UC switched to
            semesters). I learned about the prison industrial complex, the
            Moynihan Report, as well as what race <em>truly</em> is - why it
            exists, how it was/is used to subjugate specific demographics. After
            the course sequence was over, I walked away with a greater sense of
            purpose, my self-realization jumped to level 99, and for the first
            time <em>I knew who I wanted to become.</em> I felt like I was in
            the right place.
          </p>

          <p>
            Fast forward to the present. Earlier this year, Lisa Slutsky and
            Dave Dziak asked for me to attend a demo day at MAX Technical
            Training after I had been accepted into the program. As a part of
            the demo day, I had the opportunity to shadow one of their classes.
            That feeling of being in the right place at the right time? Yeah,
            that's how I felt while sitting in that classroom, glued to every
            word the instructor said as they showcased to the class how to use
            JavaScript with HTML and CSS. And now, four weeks into the coding
            bootcamp, I can confirm that it was well worth the wait. I am right
            where I need to be, and I have a chance to make something of it.
          </p>

          <p>All right, enough of that. On to the good stuff!</p>

          <h3 id="day1">Day 1 Reflection: Weird Sentence, But Ok</h3>

          <p>
            Tuesday's class was general instruction on Chapter 4 and giving us
            the opportunity to meet control statements. This topic includes
            if/else statements (LOVE THEM), while loops, do-while loops and
            coding break and continue statements. Much of this, like the if/else
            statements and loop structures, is not all that new to me; Brandon
            covered all of these throughout our meetings leading up to the
            bootcamp. I definitely have some familiarity with each of them.
          </p>

          <p>
            Starting off, the first of the chapter, "How to Code Boolean
            expressions", talks about the eight different data types that can be
            used in Java. As a way to remember all of them, I came up with a
            really silly run-on sentence: Bite shorts long into floatable
            doubles and the characters boo.
          </p>

          <p>Okay, hear me out.</p>

          <p>
            I get it. That sentence makes ABSOLUTELY no sense whatsoever. BUT
            (and there's always a but) it is effective.
          </p>

          <p>What are each of the data types, you ask? Well, they are:</p>

          <ol>
            <li>Byte (bite)</li>
            <li>Short (shorts)</li>
            <li>Long (long)</li>
            <li>Int (into)</li>
            <li>Float (floatable)</li>
            <li>Double (doubles)</li>
            <li>Char (character)</li>
            <li>Boolean (boos)</li>
          </ol>

          <p>
            Here's a good question: should I remember all of these data types by
            heart? Is this something I <em>need</em> to do? Yes. Yes, I should
            remember all of these. As stupid as that sentence sounds, you better
            believe it's drilled into my memory. And it's fun! What's not to
            like about stringing words together to form incoherent sentences?
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="day2">Day 2 Reflection: The Powers That Be</h3>

          <p>
            It's time for an example of actual code. During the first hour and a
            half of class on Thursday (11/15), we were given some time to work
            on the projects laid out in chapter four of our book. The example I
            am going to use is a program that calculates and displays the user's
            chosen number to the second and third power.
          </p>

          <CodeExample
            codeString={`
public class TableofPowersApp {

    public static void main(String[] args) {
        // TODO Auto-generated method stub
        
        // display welcome message
        System.out.println("Welcome to the Aperture Science Companion Cubes Calculator!");
        System.out.println(); // new line for space
        
        // create scanner
        @SuppressWarnings("resource")
        Scanner sc = new Scanner(System.in);
        
        // create choice variable
        String choice = "y";
        
        // create while loop for logic
        while(choice.equalsIgnoreCase("y")) {
            
            // prompt user for inputs
            System.out.print("Enter an integer: ");
            int digit = sc.nextInt();
            System.out.println(); // new line for space
            
            // create for loop to iterate over outcomes for digit
            int i;
            
            for (i = 0; i <= digit; i++) {
                int squared = (i * i);
                int cubed = i * i * i;
                
                // display results
                System.out.print("Number: " + i + " " + 
                "Squared: " + squared + " " + 
                "Cubed: " + cubed + "\\n");
                
                ...
                    }
                }
          `}
          />

          <p>
            If you read the last post at all, then you{" "}
            <a href="https://goomba5.github.io/november/blog2" target="_blank">
              surely recognize the layout of the code
            </a>{" "}
            in this bit. As is custom, it starts off with the welcome message,
            followed by our scanner instance so the console can collect an
            input, and then we move on to the while loop and the logic. Pretty
            straightforward.
          </p>

          <p>
            With this project, however, we had to use a <code>for loop</code> to
            calculate the input to the second and third power. To do that, we
            had to create the loop by stating
          </p>

          <CodeExample
            codeString={`
            int i;
            
            for (i = 0; i <= digit; i++)
          `}
          />

          <p>
            Now, it is important to note that the <em>counter</em>, i, is
            declared outside of the for loop statement (<code>int i;</code>).
            The reason for this is because we want to be able to display not
            just the powers of the input, but the actual number being
            multiplied. If i were initialized inside of the for loop as{" "}
            <code>int i = 0</code>, then it would not be accessible for us to
            display in the results because its scope would be limited to the
            loop. Instead, we widen its scope by declaring it outside of the for
            loop, and then initialize it.
          </p>

          <p>
            With the for loop all set and ready to go, the{" "}
            <code>int squared</code> and <code>int cubed</code> variables are
            prepped for the products. When the program runs, the user will be
            prompted to enter an integer. The user's input is captured in the{" "}
            <code>int digit</code> variable, which is used in the for loop to
            determine which numbers will be displayed with their squared and
            cubed values. If the user enters the integer 3, then the for loop
            will process the second and third powers for the numbers 1, 2 and 3,
            which will then be displayed in the console. And, like the previous
            projects, the program will ask if the user wants to continue or not.
            (<em>Nerd Alert:</em> if you enter 'n', then it'll say thank you for
            using this Aperture Science program.)
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3 id="day3">Day 3 Reflection: Error of Your Ways</h3>

          <p>
            Looking back on the class instruction from last Saturday, the amount
            of new material we covered hit us like an avalanche. There was so
            much to take in that my brain was still in a spin-cycle long after
            it had ended.
          </p>

          <p>
            How did class go this time around? Well, I'm still standing! My
            brain didn't feel like it ran a beep test.
          </p>

          <p>
            Today's session was all about testing, debugging and deploying an
            application. For this part of the post, I really want to focus on
            the types of errors that can occur in Java. Before this journey to
            becoming a software developer started, the term 'bug' was the most
            often heard reference to something being broken or not working as
            expected. Pulling from work experience for a moment, the number of
            times I have heard the word bug from panicked real estate agents has
            become a trigger.
          </p>

          <p>
            With Java, there are three types of errors that can occur in a
            program:
          </p>

          <p>
            <strong>A syntax error</strong> (or compile-time error) occurs when
            your IDE notices things like spelling errors in your variable names
            or keywords. Maybe there's a closing parenthesis missing or, as I'm
            quickly learning, a missing brace (UGGHHHH IT'S THE WORST).
          </p>

          <p>
            The second type is a <strong>runtime error</strong>, which means all
            of your syntax is great, but your application, says, Hey, buddy, I'm{" "}
            <em>throwing</em> this exception and you need to <em>catch</em> it
            before anything else bad happens. In Java,{" "}
            <a
              href="https://docs.oracle.com/javase/8/docs/api/index.html?java/lang/Exception.html"
              target="_blank"
            >
              there are a lot of exception classes
            </a>{" "}
            that may be thrown.
          </p>

          <p>
            If the first type of error is related to syntax errors and the
            second type is when the application prematurely ends, then what
            would the third type of error be? The third type is a{" "}
            <strong>logical error</strong>. An example would be a calculator
            application that is supposed to print the product of two inputs from
            the user in the console, but the actual output does not match the
            expected output. Instead of your application confirming that 2 * 3 =
            6, the actual output is presenting the product as 7. Why is 2 + 2
            presenting 5 when it should be 4? There must be a logical error in
            the application.
          </p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>

          <h3>Rounding Third</h3>
          <p>
            Each week has presented its own challenges so far, and it has been
            absolutely welcomed. There's always something to look forward to and
            study, something new to implement, another piece to add to the
            puzzle. This week was no different.
          </p>

          <p>Thank you for reading, and I'll see you next time.</p>

          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
        </div>
      </div>
    );
  }
}

export default NovBlog3;



