import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class DecBlog2 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>I'm On A Plane</h2>
          <p>Official Post Date: 12/07/18</p>
          <h3>Contents</h3>
          <ol>
            <li>
              <a href="#intro">Brief Introduction (In the Moment)</a>
            </li>
            <li>
              <a href="#sign">A Sign of Things to Come</a>
            </li>
            <li>
              <a href="#nope">The Part Where I Do Not Have It</a>
            </li>
            <li>
              <a href="#missed">Missed Connection</a>
            </li>
            <li>
              <a href="#moral">Moral of the Story</a>
            </li>
          </ol>
          <h3 id="intro">Brief Introduction (In the Moment)</h3>
          <p>
            Right now - and I'm guessing, so forgive me - I am <em>about</em>{" "}
            20,000 feet off of the ground. It's pitch black outside, there are
            engines whirring with maximum efficiency to propel us to our
            destination.
          </p>
          <p>I'm on a plane.</p>
          <p>
            As I had mentioned in the last post, the ZG18 event took place this
            week. For me, ZG week is a great opportunity to be outside of my
            comfort zone. Starting with last year's event, there are half-hour
            long conference sessions where presenters from companies within
            Zillow Group touch on different aspects of the work they do, how it
            applies to ZG's mission and the broader takeaways that could prove
            useful to others. It's a chance to listen and to learn more about
            the people who make Zillow Group function. But back to the main
            point.
          </p>
          <p>
            This post is not going to be like the others. Remember, I'm writing
            this while on a plane ride home (4am EST). I'm doing the whole,
            "Let's-do-a-spontaneous-post-on-a-plane" thing, which, I know, is
            not a thing. It seems like the right idea. Like getting Skyline at
            1am because it "sounds great", when you know full well your body and
            soul are protesting as hard as they can <em>not</em> to go. They're
            woke, but you're not.
          </p>
          <p>Here was the itinerary for Tuesday:</p>
          <ol>
            <li>Flight departs from CVG to O'Hare at 11:28am EST</li>
            <li>Flight departs O'Hare to Seattle at 1:55pm CST</li>
            <li>Arrival time in Seattle: 4:55pm PST</li>
          </ol>
          <p>Now, let's look at how it all went wrong.</p>

          <h3 id="sign">A Sign of Things to Come</h3>
          <p>
            My mom being the awesome person she is offered to take me to CVG.
            The original plan was to be at the airport at 10am so that I could
            check in, do the TSA stuff and then have time to study before the
            flight. Simple plan, right? Yup. Simple.
          </p>
          <p>
            We're driving down 75 S toward the airport. Mom and I were talking
            about something. I think the conversation was about her house and
            how we could clean it up, make it nice. We were about fiften or so
            minutes into the drive at this point and my mind was tuning in and
            out. But I noticed the sign coming up for the airport exit less than
            a mile out.
          </p>
          <p>
            We continued to talk and at this point the exit is about a quarter
            of a mile away. She's still talking about the house, how the
            original company that was supposed to pick up a few pieces of
            furniture rejected the offer.
          </p>
          <p>
            Less than a quarter of a mile from the exit. There's the exit. We
            passed the exit.
          </p>
          <p>"Hey, mom. You passed the airport exit."</p>
          <p>"Did I really? Shoot!"</p>
          <p>
            If I have been keeping count like a good son would, then that was
            mom turn-around number 1,355,872. It was a sign of things to come.
          </p>
          <p>
            <em>
              It's okay. It's just the exit to the airport. Nothing{" "}
              <strong>major</strong>. We got this.
            </em>
          </p>
          <p>But I so did not have it.</p>

          <a href="#backToTop">Back to Top</a>

          <h3 id="nope">The Part Where I Do Not Have It</h3>
          <p>
            It's about 10:25am on Tuesday, and I'm sitting at gate B24 in CVG.
            The plane is supposed to board at 11:28am and officially leave at
            11:55am.
          </p>
          <p>
            Delay #1: Flight 3707 has parked at our gate and is letting the
            passengers off the plane. As the last person leaves the tunnel, the
            desk attenddant gets on the mic.
          </p>
          <p>
            "For those waiting to board flight 3707 to Chicago the flight has
            been delayed to 12:10pm this afternoon because of maintenance. We do
            apologize for the inconvenience. Thank you for your patience."
          </p>
          <p>
            I thought to myself, Okay, that's not so bad. All things considered,
            I'd arrive in Chicago around 12:40pm CST, well before my next flight
            at 1:55pm CST. We're good.
          </p>
          <p>
            "Members on flight 3707, I do apologize. Our maintenance crew is
            still looking into the issue, so the flight will not leave until
            12:50pm. Again, we do apologize for the inconvenience and thank you
            your patience."
          </p>
          <p>
            Okay. 12:50pm? Now I had some doubt. What did the maintenance crew
            find? And so it began. Egencia call number 1: the rep checked the
            flights and suggested I stick it out and wait for flight 3707 to be
            repaired. If anything went wrong, then they recommended giving 'em
            another call (READ: I WOULD BE CALLING THEM AGAIN).
          </p>
          <p>
            It's about 12:38pm. My anxiety is not high, but it sure as hell
            isn't low. Seriously, what did the maintenance crew find? Was Bruce
            Willis fighting someone? Were there snakes on the plane? And that's
            when...
          </p>
          <p>
            "For those waiting on flight 3707 to O'Hare, I have some more bad
            news." (Great. Here we go.)
          </p>
          <p>
            "Our maintenance crew have told us that the flight will have to be
            delayed another 2 hours (!!!!!!!!!!!!!!!!!!!!!!!!). We really do
            apologize for the inconvenience. If you would like, we will be
            opening another help desk if you'd like to switch to a different
            flight. Again, thank you for your patience."
          </p>
          <p>
            There were no alarm bells in my head. That's how loud my anxiety
            was. The desk attendant placed the mic back on the desk, and
            proceeded to type away whatever information she needed to on the
            computer in front of her. Walking up from the tunnel was one of the
            guys from the crew investigating the issue. He picked up the mic.
          </p>
          <p>
            "Passengers on flight 3707, to be honest with you, we have found the
            issue with the plane. The delay has been extended because we found
            the windshield on the plane is cracked, therefore it is not safe to
            fly." Blah blah blah sorry.
          </p>
          <p>
            <em>A cracked windshield?!?! HOLY SH-</em>
          </p>
          <p>
            Egencia call number 2! Alexander, the Egencia rep I spoke with, is
            the <em>man</em>. After informing him that flight 3707 aka flight
            "I-don't-wanna-get-off-the-ground-you-can't-make-me", Alex was able
            to get me set up for a flight taking off at 3:30pm, which meant my
            Chicago flight would be at 5:17pm CST. We're on our way!
          </p>
          <p>
            I make my way over to gate B18 for the 3:30pm flight to O'Hare,
            feeling somewhat better about the day. I've been in the airport
            since 10am and accepted the fact that I won't be able to utilize the
            free time I had scheduled in Seattle as planned. Wah.
          </p>
          <p>
            I sit down in the first row of the seating area so when it comes
            time to board, I'll be ready. A just to be sure, I check the flight
            board across from me: flight is ON TIME. Awesome.
          </p>
          <p>
            Quick aside: the gentleman next to me, it turns out, was a
            firefighter at one point before making the career change to a
            ballroom dancer. Just goes to show that your career path can zig and
            zag in some unexpected directions. But how did I know that bit of
            information? The dancer dude was talking to <em>Jerry Springer</em>{" "}
            one seat over. Yes, I was totally listening in on their
            conversation, and it was definitely worth it. I was holding Michelle
            Obama's book Becoming like I was reading it.
          </p>
          <p>
            As their conversation ended, I took another look at the flight
            board. Once again, it had been delayed.
          </p>
          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
          <h3 id="missed">Missed Connection</h3>
          <p>
            If you haven't figured it out yet, my day was not going according to
            plan.
          </p>
          <p>
            Instead of the flight leaving at 3:30pm, it was now scheduled to
            leave at 4:10pm. That meant I would arrive at O'Hare somewhere
            around 5pm CST, and I would have to sprint to the gate to board the
            next flight.
          </p>
          <p>
            A few moments passed, and I looked at the board again. This time, it
            said 4:15pm was the flight time. NOW my anxiety was fully ramped.
            There was a real chance that I would miss my flight from O'Hare to
            Seattle if we didn't leave soon.
          </p>
          <p>
            Finally, we boarded the plane - at 4:25pm. Once everyone boarded, we
            were then told that the plane would have to have deicer sprayed on
            it. After that was over, we lifted off of the CVG tarmac at 4:45
            EST. You can see where this is going.
          </p>
          <p>
            By the time we landed in Chicago and were allowed to get off of the
            plane, it was 5:08pm CST. That's right: I had to sprint like I was
            on a soccer field chasing a player headed towards goal.
          </p>
          <p>So, I sprinted to the gate where my flight was set to leave.</p>
          <p>
            I rounded the corner and saw my gate, keeping some kind of faith
            that the flight had not left yet. But the tunnel door, I noticed,
            was closed. My stomach dropped.
          </p>
          <p>
            A moment later an attendant came from the tunnel. "Hi! Has this
            flight already left the terminal? I'm supposed to be on the flight
            to Seattle."
          </p>
          <p>
            With a concerned look, she said, "Oh, I'm so sorry. The plane has
            already boarded and prepared for take-off."
          </p>
          <p>Aaaaaaaand my stomach dropped another level. We were defeated.</p>
          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
          <h3 id="moral">Moral of the Story</h3>
          <p>Egencia call #3!</p>
          <p>
            Without sounding like a budding villain on the line, I called
            Egencia one more time. I got Alex again. I explained that the 5:17pm
            flight had left, and that I had missed my connection to Seattle.
            Alex said he'd call me back with a plan.
          </p>
          <p>
            At 6:30pm, Alex called. The options were: I could wait until
            tomorrow morning for the next flight, or jump on a flight through
            Alaskan Airlines that would leave at 7:30pm and arrive in Seattle
            between 10 and 10:30pm. I opted for the latter.
          </p>
          <p>OFFICIAL ARRIVAL TIME IN SEATTLE: 10:20pm.</p>
          <p>OFFICIAL CHECK-IN AT THE HOTEL: 11pm.</p>
          <p>My Tuesday was lost.</p>
          <p>So, what's the moral of the story?</p>
          <p>I will take the earliest flight out of CVG in the future.</p>
          <p>Thank you for reading, and I'll see you next time!</p>
          <p>
            <a href="#backToTop">Back to Top</a>
          </p>
        </div>
      </div>
    );
  }
}

export default DecBlog2;



