import React, { Component } from "react";
import { Redirect } from "react-router-dom";

// *** 2018 POSTS ***
import JuneBlog2 from "../pages/years/2018/june/Blog2";
import JulyBlog2 from "../pages/years/2018/july/Blog2";
import SeptBlog1 from "../pages/years/2018/september/blog1";
import OctBlog1 from "../pages/years/2018/october/blog1";
import OctBlog2 from "../pages/years/2018/october/blog2";
import NovBlog1 from "../pages/years/2018/november/blog1";
import NovBlog2 from "../pages/years/2018/november/blog2";
import NovBlog3 from "../pages/years/2018/november/blog3";
import NovBlog4 from "../pages/years/2018/november/blog4";
import DecBlog1 from "../pages/years/2018/december/blog1";
import DecBlog2 from "../pages/years/2018/december/blog2";
import DecBlog3 from "../pages/years/2018/december/blog3";
import DecBlog4 from "../pages/years/2018/december/blog4";
import DecBlog5 from "../pages/years/2018/december/blog5";

// *** 2019 POSTS ***
import JanBlog1 from "../pages/years/2019/january/blog1";

const posts = {
  may: {},
  june: {
    blog2: JuneBlog2
  },
  july: {
    blog2: JulyBlog2
  },
  september: {
    blog1: SeptBlog1
  },
  october: {
    blog1: OctBlog1,
    blog2: OctBlog2
  },
  november: {
    blog1: NovBlog1,
    blog2: NovBlog2,
    blog3: NovBlog3,
    blog4: NovBlog4
  },
  december: {
    blog1: DecBlog1,
    blog2: DecBlog2,
    blog3: DecBlog3,
    blog4: DecBlog4,
    blog5: DecBlog5
  },
  january: {
    blog1: JanBlog1
  }
};

class Post extends Component {
  render() {
    const {
      match: {
        params: { month, post }
      }
    } = this.props;
    const postsForMonth = posts[month];
    const BlogPost = !!postsForMonth ? postsForMonth[post] : null;

    return !!BlogPost ? (
      <BlogPost />
    ) : !!postsForMonth ? (
      <Redirect to={`/${month}`} />
    ) : (
      `There are no blog posts for ${month}`
    );
  }
}

export default Post;



