import React, { Component } from "react";
import { Link } from "react-router-dom";

class December extends Component {
  render() {
    return (
      <div className="blog">
        <div>
          <h3>Blog Posts for December</h3>
          <Link className="bloglink" to="/december/blog1">
            You Guessed It: More Java (12.2.18)
          </Link>
          <Link className="bloglink" to="/december/blog2">
            I'm On A Plane (12.7.18)
          </Link>
          <Link className="bloglink" to="/december/blog3">
            Plan Of Attack (12.16.18)
          </Link>
          <Link className="bloglink" to="/december/blog4">
            Step On the Break (12.23.18)
          </Link>
          <Link className="bloglink" to="/december/blog5">
            Step On the Break - Part 2 (12.30.18)
          </Link>
        </div>
      </div>
    );
  }
}

export default December;



