import React, { Component } from "react";

class Breadcrumbs extends Component {
  render() {
    const { links } = this.props;
    return (
      <div>
        {links.map(link => (
          <a href={link.link}>{link.label}</a>
        ))}
      </div>
    );
  }
}

export default Breadcrumbs;



