import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class OctBlog2 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>Git with the Program</h2>
          <p>Date: 10/28/18</p>
          <p>Time: 10:44pm</p>
          <h3>Git Some</h3>
          <p>Okay, that's enough puns. I promise I'll stop.</p>
          <p>
            For the first and second class of bootcamp, we covered a few of the
            commands for the Git repository system. We went over the process of
            initializing a repository, adding changes to the staging index and
            then committing those changes.
          </p>
          <CodeExample
            codeString={`
            git init
            git add
            git commit -m "initial commit"`}
          />
          <p>
            We started off by creating a folder titled "repos" through the
            command line using <code>mkdir</code>. We then created a sub folder
            called "bios", which is the directory we would initialize and use to
            connect to GitHub.
          </p>
          <p>
            First things first: we needed to create a document and make some
            changes. Our file would be called <code>bootcamp.txt</code>. The
            first sentence we entered, "Java Bootcamp is awesome", was our very
            first change. So, the next step was to add that change to our
            staging index by using the <code>git add</code> command, which
            officially moved our changes from the working stage. Entering{" "}
            <code>git status</code> showed that the <code>bootcamp.txt</code>{" "}
            file was staged and ready to commit. So far so good.
          </p>
          <p>
            Last but not least, we needed to commit our changes. By entering the{" "}
            <code>git commit -m</code> command, the snapshot of the changes made
            on the bootcamp text file are stored in the Git repository for the
            bios folder.
          </p>
          <p>
            Boom! And that was it. We made a folder, created a file, and then
            used Git to store changes in the repository. Done and done.
          </p>
          <h3>Git to the Hub, Now!</h3>
          <CodeExample
            codeString={`
            git remote add origin [GitHub URL]
            git push -u origin master
            git push
          `}
          />
          <p>(No more puns after this. Seriously. I'll quit.)</p>
          <p>
            Now that we committed changes to our repository, we needed to{" "}
            <em>push</em> those changes to a remote repository called GitHub.
            First step: set up the remote that will serve as our bridge between
            Git and GitHub. To do this, a GitHub account has to be created, and
            a repository must be set up in GitHub as well (we titled it "bios"
            to match our local directory).
          </p>
          <p>
            To start, we copy the GitHub URL from the repository (you can use
            the URL in the web address bar or the clone option). Next, in the
            command line, enter <code>git remote add origin [GH URL]</code>.
            Nothing happens, right? All is quiet on the command line. If you
            type <code>git remote -v</code>, however, you'll receive this
            message:
          </p>
          <CodeExample
            codeString={`
            origin	https://github.com/goomba5/bios.git (fetch)
            origin	https://github.com/goomba5/bios.git (push)
          
          `}
          />
          <p>
            This message tells us that our remote is ready and set to go. Cool.
          </p>
          <p>
            So the bridge between Git and GitHub is set up, but if we tried to
            push anything we'd actually receive an error message. Our next step
            was to create a way for our information - our commits - to travel
            across that bridge. To do this, we use the{" "}
            <code>git push -u origin master</code> command. This tells Git and
            GitHub that our remote is a two-way street which can push commits to
            our local repository and pull from our remote repository.
          </p>
          <p>
            Ta-daaaa! Now Git and GitHub are linked together to push and pull
            commits between the local and remote repositories. Whatever changes
            are commited can now be pushed to GitHub from the local Git
            repository using <code>git push</code>. You can even use{" "}
            <code>git pull</code> to pull any commits from the GitHub repo as
            well. And you're done!
          </p>
          <h3>Awwww CRUD</h3>
          <p>
            For class on Saturday, we did a deep dive into SQL and using the
            MySQL Workbench application to practice running SQL scripts.
          </p>
          <p>
            This. Stuff. Is. AWESOME. And SQL and SQL Workbench are both free to
            download. There is still good in the world.
          </p>
          <p>
            Okay, staying focused. Today we took several steps: we created a
            database in SQL (nothing major), created a table (super small),
            inserted some values into the table and then proceeded to practice
            using SELECT statements. Let's take look at some examples.
          </p>
          <CodeExample
            codeString={`
            --create the database
            CREATE DATABASE mma;
            USE mma;

            --create the table
            CREATE TABLE Product (
                ProductID	INT  PRIMARY KEY AUTO_INCREMENT,
                Code        VARCHAR(10)     NOT NULL UNIQUE,
                Description VARCHAR(255)    NOT NULL,
                ListPrice   DECIMAL(10,2)   NOT NULL
              );

            --insert values into the table
            INSERT INTO Product VALUES
            (1, 'java', 'Murach''s Java Programming', '57.50'),
            (2, 'jsp', 'Murach''s Java Servlets and JSP', '57.50'),
            (3, 'mysql', 'Murach''s MySQL', '54.50'),
          `}
          />
          <p>All right, let's break all of this down.</p>
          <p>CREATE DATABASE</p>

          <p>
            Just as it says: create the database that will house your tables.
          </p>

          <p>CREATE TABLE</p>

          <p>
            To create a table - the spreadsheet that contains the data - we use
            the <code>Create Table</code> command. For each of the columns
            (ProductID, Code, Description) we must determine its data type. Is
            it a string? Is it an integer? A floating number? We have to define
            its type so that it is allocated within memory.
          </p>

          <p>INSERT</p>
          <p>
            Aaaaand <code>insert</code>. The insert command will add the
            information we want for each of the rows in our table. As best
            practice, after entering <code>insert into</code>, state the
            specific columns where the data is being stored.
          </p>

          <p>CONCLUSION</p>

          <p>
            This was an awesome first week. The topics we'll cover will
            definitely become more complex as we go along, but this is exactly
            what I want.
          </p>

          <p>Let's goooooo!!!</p>
        </div>
      </div>
    );
  }
}

export default OctBlog2;



