import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class OctBlog1 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>First Day of School (6th Edition!)</h2>
          <p>Date: 10/24/18</p>
          <p>Time: 8:17pm</p>

          <h3>Starting Line</h3>

          <p>
            As of this month, it has officially been one year since the pilot
            program interview.
          </p>

          <p>
            September of last year was when Rebekah and Corina had contacted me
            about scheduling an interview. General feeling at the time?
            Disbelief coupled with an overwhelming nervousness. I pinned the
            email in my inbox and read it <em>at least</em> five to six times
            each day. I felt like I had no business applying, seeing as Dotloop
            is a part of Zillow Group and - surprise - they attract a lot of
            smart, capable people.
          </p>

          <p>
            The week leading up to the interview I was a nervous wreck. Despite
            investing a large amount of time watching videos on Lynda.com,
            reading every article possible on w3schools and poring over books
            from Amazon, there was still doubt. All of the hours spent after
            work and the numerous visits to the office on off days to use the
            new white boards after the renovations were complete on five...none
            of it felt like it was enough.
          </p>

          <p>
            At the time, construction was taking place on the fifth floor, so
            the interview was held on four in the original Fish Bowl. It had all
            of the qualifications of a conference room; there was a giant TV and
            camera, a large glass table, (un)comfortable chairs, and the hidden
            perk of becoming a sauna after ten minutes. Today, with the
            renovations having been completed, the Fish Bowl (now on the fifth
            floor) is nice and roomy, a far cry from the closet it once was.
          </p>

          <p>
            When Melissa Rogers, our current customer service manager, first
            started at Dotloop, she had scheduled 1-on-1 meetings with each rep.
            During our meeting, I told her about the interview and how I hadn't
            heard back yet. This was a month after it had taken place. She
            suggested I send an email to Rebekah and Corina thanking them for
            the opportunity and ask when the decision would be made.
          </p>

          <p>
            I sat down at my desk, pulled up my email and clicked to compose a
            message. Before I could type anything, a notification appeared at
            the top corner. It was a message from Rebekah.
          </p>

          <p>I was in disbelief before, but this? I was at a complete loss.</p>

          <p>I pinned that message, too.</p>

          <h3>The Journey Ahead</h3>

          <p>
            I wanted to add this post today since yesterday was so special. Now
            that the first day is done and dusted, it's time to settle in for
            the ride ahead.
          </p>

          <p>
            Class has started, and with it will come a lot of exciting
            opportunities to share some anecdotes. Moving forward,{" "}
            <strong>
              {" "}
              I am going to post a blog entry every Sunday afternoon.{" "}
            </strong>{" "}
            These entries will summarize the material covered over the past
            week, any interesting discussions we may have in class, as well as
            favorite stories that may have occurred inside or outside of class.
          </p>

          <h3>Going for Goal</h3>

          <p>
            There are going to be a lot of ups and downs accompanied by plenty
            of bumps and bruises over the course of the next six months. May is
            a long ways away. I am truly looking forward to learning everything
            I can during the bootcamp and tackling the challenges that lie
            ahead. With that, I'll talk to you soon.
          </p>
        </div>
      </div>
    );
  }
}

export default OctBlog1;



