import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Route, Redirect, Switch } from "react-router-dom";

class BlogPosts extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <Link className="bloglink" to="/years/2018">
            <h3>2018 Coding Posts: First Time for Everything</h3>
          </Link>
          <p>
            2018 was a year of firsts: first time creating a static site (thank
            you, Brandon!); first time writing a blog post (cringe worthy); and,
            I finally had a chance to push some of my first changes to GitHub.
            All of the posts for 2018 came from a place of uncertainty and
            excitement, with a whole lot of learning along the way.
          </p>

          <Link className="bloglink" to="/years/2019">
            <h3>2019 Coding Posts</h3>
          </Link>
          <p>
            2019 has just begun, but the journey to become a better developer
            continues. Plenty of bumps and bruises will come in the months
            ahead.
          </p>
        </div>
      </div>
    );
  }
}

export default BlogPosts;



