import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class JulyBlog2 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Objects</h2>
          <p>Date: 7/24/18</p>
          <p>Location: Longworth Hall</p>

          <h3>A (Somewhat) Fresh Start</h3>
          <p>
            July is coming to a close, which means the call volume in the
            customer service department is, to the relief of many, on the down
            slope. Our total daily call numbers confirm this trend, so we are
            all getting a chance to take a breather and really enjoy the down
            time. Hoorayyyyy!
          </p>

          <p>
            In addition to that good news, I will be working in Account
            Operations (AOOOOOOO) each Wednesday from 8am-4pm and then meeting
            with Brandon for the last hour of the day. We finally - FINALLY! -
            are going to have the meetings we were guaranteed when all of this
            began. This is a welcomed change, to say the least.
          </p>

          <p>
            With the decline in call volume and schedule changes, that down time
            has been put to great use with this week's the homework assignment.
            This past week has been all about objects: what they are, how to use
            them, and the different ways they can be accessed. So, without
            further adeu...
          </p>

          <h3>Objectively Speaking</h3>
          <CodeExample
            codeString={`
          Object for Problem1
          const person = {
            firstName: "Brandon",
            lastName: "Clemons"
          };
          
          problem1(person) {
            let fullName = person.firstName + " " + person.lastName;
            return fullName;
          }

          Object for Problem2
          const people = {
            brandon: 27,
            ben: 22,
            joel: 24,
            dario: 24,
            markelle: 20,
            robert: 28
          };

          problem2(people, age) {
            let result = {};
        
            for (let personName in people) {
              const personAge = people[personName];
        
              if (personAge <= age) {
                result[personName] = personAge;
              }
            }
          `}
          />

          <p>
            For a lot of people, computers serve an important role in every day
            life. Computer systems in cars allow drivers to use bluetooth
            technology, tell them how many miles have been traveled during a
            trip, and even control the ability of windows to open or close.
            Computers and their applications play a vital role in how goods and
            services work.
          </p>

          <p>
            So how do computers actually <em>know</em> about these different
            items in the world? How does a computer know there are four windows
            of a car or that the doors will lock/unlock or that grandma wants
            the A/C dialed to 62 degrees?
          </p>

          <p>
            All of these things are better known as <strong>objects</strong>. In
            order for a computer or an application to know what we want to
            convey to it, we have to have a way of describing all of these
            items. We do this by creating objects that contain properties and
            methods to describe the real-world item.
          </p>

          <p className="break"> * * * </p>

          <p>
            For this past week's homework assignment, I was tasked with three
            problems dealing with objects and how to access them. Problem1 and
            problem2 will be discussed in this first section.
          </p>

          <p>
            For problem1, I had to use dot notation to access the person object
            and return that information as a string. person.firstName accesses
            the value of the firstName property, and person.lastName accesses
            the value of lastName property.
          </p>

          <p>
            Problem2 takes this a step further. Using the object called people,
            I had to create a function that would return an object with the
            people who met a specific age.
          </p>

          <h3>From Object to Array and Back Again</h3>
          <CodeExample
            codeString={`
            Object for Problem3
            const people = {
              brandon: 27,
              ben: 22,
              joel: 24,
              dario: 24,
              markelle: 20,
              robert: 28
            };

            problem3(people) {

              let result = [];
              for (let person in people) {
                let personObject = {
                  age: people[person],
                  name: person
                };
          
                result.push(personObject);
              }
              return result;
            }
          `}
          />

          <p>This week's challenge was problem3.</p>

          <p>
            For problem3, the goal was to create an array of objects that
            contained each person's name and age. Simply enough. You put the
            thing in the other thing and - boom! - array of objects.
          </p>

          <p>
            Step one was to create an empty array that would contain each
            object. Step two was to create a for...in loop so that the function
            will loop through each person in the people object.
          </p>

          <p>
            Step three involved creating an object that will contain the
            key/value pairs for each person. With each key (let person), the
            personObject variable will add that person's name and age to an
            object.
          </p>

          <p>
            Step four is where things got hairy. The array that will contain
            each of the objects is there, the for...in loop and personObject
            will create and contain each person's information, but how do we put
            those objects into the array?
          </p>

          <p>
            The answer lies in the push method. Step four uses the push method
            to add each new object to the array.
          </p>

          <h3>Looking Ahead</h3>

          <p>Woooooo!! Let's go!</p>

          <p>
            I recently purchased a book from Amazon that has practice problems
            for programmers. I am super excited to start digging into that and
            putting what I learn into action.
          </p>

          <p>
            This whole process has been amazing. I am really anxious to learn
            more.
          </p>
        </div>
      </div>
    );
  }
}

export default JulyBlog2;



