import React, { Component } from "react";

class Welcome extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>Welcome!</h2>
          <h3>Yo!</h3>
          <p>
            Welcome to Training Ground! This site is nothing more than a sandbox
            for practicing, writing about my mistakes and experiences during the
            learning process on the road to becoming a software developer. Most
            importantly, this is a place where I do my best to have some fun. Be
            prepared to cringe.
          </p>

          <h2>Recent Posts</h2>

          <h3>
            <a href="https://goomba5.github.io/december/blog5" target="_blank">
              Step On the Break - Part 2
            </a>
          </h3>
          <p>
            This was the second post in the two part series that covered a silly
            binary search program and how to find the Big O. Not the most
            convincing explanation, but it was fun coming up with the character
            dialogue and seeing it in action. Try not to cringe.
          </p>

          <h3>
            <a href="https://goomba5.github.io/december/blog4" target="_blank">
              Step On the Break - Part 1
            </a>
          </h3>
          <p>
            Step On the Break was a set of goals that I set out to acheive over
            the Christmas break. In this first entry, I talk about connecting a
            Java application to a file using File I/O.
          </p>

          <p>
            Whether or not the analogy used throughout the post works is up for
            debate (read: no). I tried, okay? I tried.
          </p>
          <p>
            The welcome page should contain short descriptions and links to
            coding posts recently published, projects you are currently working
            on, and research blog posts.
          </p>
        </div>
      </div>
    );
  }
}

export default Welcome;



