import React, { Component } from "react";
import CodeExample from "../../../../components/CodeExample";

class JuneBlog2 extends Component {
  render() {
    return (
      <div className="display">
        <div className="blocks">
          <h2>The One About Getting Started</h2>

          <p>Date: 6/27/18</p>
          <p>Location: Hideout</p>

          {/* One thought/ one feeling today */}
          <h3>Summer Breeze</h3>
          <p>
            For all of us in the customer service department, we are expected to
            field thousands of calls between May and July. We're at that point
            in the year when both new and experienced agents are conducting the
            majority of their business. Finding the time to practice coding -
            let alone finding the energy - has been a major challenge, which is
            why I really wanted to create this blog. I wanted a place that would
            provide some space for me to try my hand at describing the
            interesting topics I learn about, the challenges that I face (which
            are plenty), and, most importantly, the chance to just chill out.
          </p>

          <p>So...</p>

          {/* Summary */}
          <h3>Square One</h3>
          <p>
            With Brandon's guidance today, we spent a couple of hours working to
            create the framework for this single page application by using the
            Browser Router module for React. There are two parts I will be
            focusing on with this blog entry: the importance of the Switch and
            Route components, and the process of understanding what the Post
            component does as well as how it works within the application.
          </p>

          {/* Significant Experience */}
          <h3>Switching Routes</h3>
          <CodeExample
            codeString={`
          <Switch>
            <Route exact path="/welcome" component={Welcome} />
            <Route exact path="/about" component={About} />
            <Route exact path="/may" component={May} />
            <Route exact path="/june" component={June} />
            {/* <Route exact path="/july" component={July} />
            <Route exact path="/august" component={August} />
            <Route exact path="/september" component={September} /> */}
            <Route
              exact
              path="/:month"
              render={() => "Nothing to see here. Shoo. Shoo!"}
            />
          </Switch>
          `}
          />

          <p>
            When I first set out to create this blog, there was only{" "}
            <em>one</em> React component. That one component contained most
            (read: ALL) of the sections of the site, with a comment to show
            which section you were looking at. I had one section for the
            Navigation bar, another for the Footer section as well as one for
            the Header; one large component for all of these pieces. My
            intentions were good. But was there a better way to build it? What
            would that look like?
          </p>

          <p>Spoiler alert: there is a better way to build it.</p>

          <p>
            Today, Brandon showed me how to set up each of those sections as
            React components. Each of the sections I had created - Navigation,
            Footer, Header - could actually be converted into their own
            standalone components. So he walked me through how to break it all
            down into smaller pieces, building each component and putting it all
            together.
          </p>

          <p>
            With the components in place, how do we tell the app to display the
            information the user wants to view based on the option they select?
            How would we create paths to the content so the user has the ability
            to switch between each of those different pages?
          </p>

          <p>
            Using the Switch component, the application can have defined options
            (Home, About, May) which will display values (pages and their
            content) when selected. The Switch component gives the app a way to
            present a chosen option and its content to the user.
          </p>

          <p>
            So how do we make sure the options we display will render what we
            want? Specifically, how do we create the <em>paths</em> for these
            options so they'll display the content? Well, that's where we set up
            the routes for each option using the Route component.
          </p>

          <p>
            The Route component allows us to tell our application where to go
            when an option is selected. For the blog, I want each of the buttons
            in the Navigation component - Home, About, May, June - to all
            display information that pertains to those options. For example,
            when June is selected in the Navigation bar, the June component will
            render when the file path matches exactly what we tell it ("/june")
            when the user clicks it.
          </p>

          <p>
            And this is what makes single page applications (SPA) so attractive:
            you don't have to link to seprate HTML pages and wait for the
            browser to produce the page you want. React allows the user to
            switch between the pages with little to no load time because all of
            these components are readily available on the client side. When
            interacting with the blog, each page will transition seemlessly to
            the next with little to no interruption thanks to the Switch and
            Route components.
          </p>

          <p>
            The process of creating separate components, and learning how the
            Switch and Route modules work together, was such an awesome
            exercise. Testing each of the Navigation buttons to make sure they
            worked was so much fun.
          </p>

          <p>Now the fun part...</p>

          {/* Challenge */}
          <h3>Post Challenge</h3>
          <CodeExample
            codeString={`
            1.)
            const posts = {
              may: {},
              june: {
                blog1: JuneBlog1
              }
            };

            2.)
            class Post extends Component {
              render() {
                const {
                  match: {
                    params: { month, post }
                  }
                } = this.props;
                
            3.)
                const postsForMonth = posts[month];
                const BlogPost = !!postsForMonth ? postsForMonth[post] : null;

            4.)
                return !!BlogPost ? (
                  <BlogPost />
                ) : !!postsForMonth ? (
                  <Redirect to={/$/>{month>}/} />
                ) : (
                  There are no blog posts for $/{month}
                );
              }

              export default Post;
          `}
          />
          <p>The Post component. Where to start...</p>

          <p>
            If you look at the code for the Post component above, you'll see
            that there's a lot going on. Let's start from the top.
          </p>

          <p>
            1.) The first portion you see is an object called "posts" (const
            posts), which contains the properties of the blog posts for the
            website. This is important because we will access the blog entry
            content for each month based on the key/value pairs
            (blog1:JuneBlog1, blog2:JuneBlog2, etc) within the posts object.
            Easy enough.
          </p>

          <p>
            This next part is where I got lost. Hopefully I don't butcher this
            explanation.
          </p>

          <p>
            2.) With the posts object created to give us a map of our blog
            entries, we need to create a way for our application to access those
            blog posts from the posts object. The first part of the code
            officially creates the Posts component - easy stuff. Within the
            Posts component, the code starting with the render method serves an
            important role in how the app will access our posts object. Using
            object destructuring (params: month, post), we will be able to pass
            through which properties are accessed and displayed from our posts
            object based on the key (month) and the value (post). With this
            mechanism, as you will see in just a moment, we can determine what
            will display based on which properties are passed.
          </p>

          <p>
            3.) Before we get too far ahead, we need to create a couple of
            variables. The first one is postsForMonth (const postsForMonth),
            which is equal to the key in our posts object using bracket
            notation.
          </p>

          <p>
            The next variable, const BlogPost, contains a ternary operator
            (shorthand for an if/else statement). This ternary operator says, IF
            postsForMonth is TRUE - meaning there is a key in our object
            (month), THEN access posts (postsForMonth[post]).
          </p>

          <p>We're almost there, I promise</p>

          <p>
            4.) This is where we tell the app <em>what to render</em> when an
            option is clicked. We have three options dependent upon BlogPost:
          </p>

          <dl>
            <dt>
              <em>
                <strong>BlogPost</strong>
              </em>
            </dt>
            <dd>
              If Blogpost is TRUE (!!Blogpost) - there is both a key (month) and
              value (post), THEN (?) display posts for that month.
            </dd>
            <br />
            <dt>
              <em>
                <strong>Redirect module</strong>
              </em>
            </dt>
            <dd>
              If postsForMonth is TRUE (!!postsForMonth), THEN the page will
              redirect to the selected page for the month. Because there is a
              key (month) but no value (post), the page will redirect and stay
              on the given month.
            </dd>
            <br />
            <dt>
              <em>
                <strong>There are no posts</strong>
              </em>
            </dt>
            <dd>
              If there is no key (month) and no value (post), then the app will
              display a message which states there are no blog posts for that
              month.
            </dd>
          </dl>

          <h3>Are We There Yet?</h3>

          <p>Yeah. You can go home now.</p>

          {/* Looking forward */}
          <h3>Next Steps</h3>
          <p>
            Continue to practice. Seriously, continue to practice the
            programming side of things and enjoy yourself.
          </p>

          <p />

          {/* Conclusion */}
          <h3>Conclusion</h3>
          <p>
            This is just the beginning. There is a lot to look forward to as
            time goes on.
          </p>

          <p>Remember: progress over perfection.</p>
        </div>
      </div>
    );
  }
}

export default JuneBlog2;



